.display-container{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    background-color:rgb(5, 5, 62);
    height: 80vh

}
h1{
    font-size: 100px;
    color:white;
}

.display-name{
    position:absolute;
    /* it will overflow element */
    top: 35%;
}
.display-name:nth-child(1) {
    z-index: 12000;
}

.display-name:nth-child(2){
    transform: translateX(6px);
    transform: translateY(6px);
    color: blue;
    z-index: 10000;
}
.display-name:nth-child(3){
    transform: translateX(9px);
    transform: translateY(9px);
    color:chocolate;
    z-index: 8000;
}
.display-name:nth-child(4){
    transform: translateX(12px);
    transform: translateY(12px);
    color:yellowgreen;
    z-index: 6000;
}