.navbar-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color:rgb(112, 15, 15);
    height: 20vh;
    width: 100%;
    row-gap: 35px;
    margin: 0px ;
    padding: 0px ;
    justify-content: center;
}
.name{
    font-size: 20px;
    outline: none;
    border-radius: 30px;
    border: 2px dotted rgb(227, 24, 24);
    background-color: transparent;
    padding: 10px;
}
